import {
  Component, ElementRef, OnInit, Renderer2, ViewChild } from '@angular/core';
import { GlobalService } from '../../../../../shared/services/global.service';
import { HttpClient } from '@angular/common/http';
import {  NgxSpinnerService } from 'ngx-spinner';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { NotificationService } from '../../../../../shared/services/notification.service';

@Component({
  selector: 'app-kyc-upload',
  templateUrl: './kyc-upload.component.html',
  styleUrls: ['./kyc-upload.component.scss']
})

export class KycUploadComponent implements OnInit {

  @ViewChild('video', { static: true }) videoElement: ElementRef;
  @ViewChild('canvas', { static: true }) canvas: ElementRef;
  @ViewChild('autoShownModal', { static: false }) autoShownModal: ModalDirective;
  isModalShown = false;
  documentTitle;
  documentImg;
  getUserr;
  videoWidth = 200;
  videoHeight = 200;
  iddocs;
  adddocs;
  otherdocs;
  iddocname;
  adddocname;
  othdocname;
  updateKycMsg;
  kycMessageShow = false;
  kycErrorMsg = false;
  cameraShow = false;

  kycParams = {
    ProfileId: localStorage.getItem('LoggedDealermasterid'),
    KYC_IDProof: '',
    KYC_AddrProof: '',
    KYC_Others: ''
  };
  constraints = {
    video: {
      facingMode: 'environment',
      width: {
        ideal: 4096
      },
      height: {
        ideal: 2160
      }
    }
  };

  getUser: any;
  showModal(title): void {
    this.isModalShown = true;
    this.documentTitle = title;
    this.getUserr = JSON.parse(localStorage.getItem('LoggedUser'));

    let doc = localStorage.getItem('kycdoc');
    let docc = JSON.parse(doc);
    if (doc) {

      if (title == 'ID proof') {
        this.documentImg = docc.KYC_IDProof;
      }
      if (title == 'Address proof') {
        this.documentImg = docc.KYC_AddrProof;
      }
      if (title == 'Other proof') {
        this.documentImg = docc.KYC_Others;
      }
    } else {
      if (title == 'ID proof') {
        this.documentImg = this.getUserr.sProfile.uKYCInfo.ID_PRROF;
        console.log(this.documentImg);
      }
      if (title == 'Address proof') {
        this.documentImg = this.getUserr.sProfile.uKYCInfo.ADDR_PROOF;
        console.log(this.documentImg);
      }
      if (title == 'Other proof') {
        this.documentImg = this.getUserr.sProfile.uKYCInfo.Others;
        console.log(this.documentImg);
      }
    }

  }

  hideModal(): void {
    this.autoShownModal.hide();
  }

  onHidden(): void {
    this.isModalShown = false;
  }

  constructor(
    private notificationService: NotificationService,
    private renderer: Renderer2,
    private global: GlobalService,
    private http: HttpClient,
    private spinner: NgxSpinnerService
    ) {

    this.getUser = JSON.parse(localStorage.getItem('LoggedUser'));

  }

  handleError(error) {
    console.log('Error: ', error);
  }
  attachVideo(stream) {

    this.renderer.setProperty(this.videoElement.nativeElement, 'srcObject', stream);
    this.renderer.listen(this.videoElement.nativeElement, 'play', (event) => {
      this.videoHeight = this.videoElement.nativeElement.videoHeight;
      this.videoWidth = this.videoElement.nativeElement.videoWidth;
      this.spinner.hide();
      this.cameraShow = true;
    });
  }
  startCamera() {

    this.spinner.show();
    if (navigator.getUserMedia) {
      navigator.mediaDevices.getUserMedia(this.constraints).then(this.attachVideo.bind(this)).catch(this.handleError);
    } else {
      alert('Sorry, camera not available.');
    }
  }

  open_camera(documentType) {

    this.startCamera();

    if (documentType == 'id') {
      this.iddocs = 'id';
    }

    if (documentType == 'add') {
      this.adddocs = 'add';
    }

    if (documentType == 'oth') {
      this.otherdocs = 'oth';
    }

    console.log(this.iddocs + 's' + this.adddocs + 'a' + this.otherdocs);
  }

  capture(c) {
    this.spinner.show();

    this.renderer.setProperty(this.canvas.nativeElement, 'width', this.videoWidth);
    this.renderer.setProperty(this.canvas.nativeElement, 'height', this.videoHeight);
    this.canvas.nativeElement.getContext('2d').drawImage(this.videoElement.nativeElement, 0, 0);

    const data = {
      App: 'uploadReceit',
      oData: c.toDataURL()
    };

    // tslint:disable-next-line: max-line-length
    this.http.post('http://client.traderscabinet.com/tr_test/assets/PHP/alfa/v1/common/UploadDipositPaymentReceipt', data).subscribe((res: any) => {

      if (res) {
        this.spinner.hide();
      }

      if (this.iddocs == 'id') {
        this.kycParams.KYC_IDProof = res.resp.url;
        localStorage.setItem('idproof', res.resp.url);
      }
      if (this.adddocs == 'add') {
        this.kycParams.KYC_AddrProof = res.resp.url;
        localStorage.setItem('addproof', res.resp.url);
      }
      if (this.otherdocs == 'oth') {
        this.kycParams.KYC_Others = res.resp.url;
        localStorage.setItem('othproof', res.resp.url);
      }
    });

  }

  ngOnInit() {}


  encodeImageFileAsURL(element: HTMLInputElement, documentType, fileInput: Event) {

    if (documentType == 'id') {
      this.iddocname = (fileInput.target as HTMLInputElement).files[0].name;
    }
    if (documentType == 'add') {
      this.adddocname = (fileInput.target as HTMLInputElement).files[0].name;
    }
    if (documentType == 'oth') {
      this.othdocname = (fileInput.target as HTMLInputElement).files[0].name;
    }

    this.spinner.show();
    this.cameraShow = false;

    console.log(element);
    var file = element.files[0];
    var reader = new FileReader();
    reader.readAsDataURL(file);

    reader.onload = () => {

      console.log('RESULT', reader.result);
      const data = {

        App: 'uploadReceit',
        oData: reader.result

      };

      // tslint:disable-next-line: max-line-length
      this.http.post('http://client.traderscabinet.com/tr_test/assets/PHP/alfa/v1/common/UploadDipositPaymentReceipt', data).subscribe((res: any) => {
        if (res) {
          this.spinner.hide();

        }
        if (documentType == 'id') {
          this.kycParams.KYC_IDProof = res.resp.url;
          localStorage.setItem('idproof', res.resp.url);
        }
        if (documentType == 'add') {
          this.kycParams.KYC_AddrProof = res.resp.url;
          localStorage.setItem('addproof', res.resp.url);
        }
        if (documentType == 'oth') {
          this.kycParams.KYC_Others = res.resp.url;
          localStorage.setItem('othproof', res.resp.url);
        }
      }, err => {
        this.spinner.hide();
      });
    };

  }


  updateKYC() {


    // if ((this.kycParams.KYC_IDProof == '') || (this.kycParams.KYC_AddrProof == '')) {

    //   this.kycErrorMsg = true;
    //   setTimeout(() => {
    //     this.kycErrorMsg = false;
    //   }, 10000);


    // } else {

    let id = localStorage.getItem('idproof');
    let add = localStorage.getItem('addproof');
    let oth = localStorage.getItem('othproof');

    if (id) {
      this.kycParams.KYC_IDProof = id;
    }
    if (add) {
      this.kycParams.KYC_AddrProof = add;
    }
    if (oth) {
      this.kycParams.KYC_Others = oth;
    }

    this.spinner.show();
    this.global.editUser(this.kycParams).subscribe((kyc: any) => {
        localStorage.setItem('kycdoc', JSON.stringify(this.kycParams));
        if (kyc.Result) {
          this.global.loadData();
          this.spinner.hide();
          this.updateKycMsg = kyc.MSG_USER;
          this.kycMessageShow = true;
          this.notificationService.showSuccess('Profile KYC has been updated successfully.', 'Success');
          setTimeout(() => {
            this.kycMessageShow = false;
          }, 10000);
        }
        console.log(kyc);

      }, err => {
        this.notificationService.showError(this.updateKycMsg, 'Error');

        console.log(err);
        this.spinner.hide();

      });
    // }
  }
}
