import { KycUploadComponent } from './documents-core/kyc-upload/kyc-upload.component';
import { DocumentsCoreComponent } from './documents-core/documents-core.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  {
    path: '',
    component: DocumentsCoreComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'kyc-upload'
      },
      {
        path: 'kyc-upload',
        component: KycUploadComponent,
        data: {title: 'KYC Upload', breadcrumb: 'KYC Upload'}
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DocumentsRoutingModule { }
